<template>
  <div class="my">
    <div class="bg" @click="showPopup" :style="{backgroundImage: `url('${userData.userBg}')`}"> 
    </div>

    <div class="content">
      <div v-if="!isLogin" class="unlogin">
        <!-- <span>去注册 / 登录</span> -->
        <router-link to="{name: 'Login'}">去注册 / 登录</router-link>
      </div>
      <div v-else class="content-top clearfix">
        <div class="img-box fl">
          <img class="auto-img" :src="userData.userImg" alt="">
        </div>
        <div class="info-box fl">
          <div class="name ">
            {{userData.nickName}}
          </div>
          <!-- <div class="other">
            黄金会员
          </div> -->
          <div class="desc">
            {{userData.desc}}
          </div>
        </div>
      </div>
      <div class="content-middle">
        <van-cell :title="item.title" v-for="(item,index) in list" :key="index" is-link  @click="toline(item.name)"/>
      </div>
      <div class="content-bottom" v-if="isLogin">
        <van-cell @click="showDeletePopup">退出登录</van-cell>
      </div>
    </div>

    <van-popup v-model="show" round position="bottom" :style="{ height: '60px' }">
      <div class="popup-box">
        <div class="title">修改背景</div>
        <van-uploader :after-read="updateUserBg" />
      </div>
    </van-popup>

  </div>
</template>

<script>
  import "../../assets/less/my.less"
  import {utils} from "../../assets/js/utils"

  export default {
    name:"My",
    data(){
      return{
        list:[
          {title:'我的订单', name:'Order'},
          {title:'我的收藏', name:'Favor'},
          {title:'个人中心', name:'Account'},
          {title:'地址管理', name:'Address'},
          {title:'安全中心', name:'Secure'},
        ],
        userData: {},
        isLogin: false,
        show: false,
        maxSize: 1 * 1024 *1024,
      }
    },
    created(){
      this.getuserData();
    },
    methods:{
      getuserData(){
        let tk = localStorage.getItem('CSTK');
        // 不存在token
        if(!tk){
          return;
        }
        
        this.$toast.loading({
          message: '加载中...',
          forbidClick: true,
          duration: 0,
          loadingType: 'spinner'
        })

        this.axios({
          method: "GET",
          url: "/findMy",
          params: {
            appkey: this.appkey,
            tokenString: tk
          }
        }).then( result => {
          this.$toast.clear();
          if(result.data.code == "A001"){
            this.isLogin = true;
            this.userData = result.data.result[0];
          }else{
            this.isLogin = false;
            this.userData = [];
          }
          // console.log("findMy result ==> ",result)
        }).catch( err => {
          this.$toast.clear();
          // console.log("findMy err ==> ",err)
        })
      },
      showPopup(){
        this.show = true;
      },
      updateUserBg(file){
        let tk = localStorage.getItem('CSTK');
        // 不存在token
        if(!tk){
          return;
        }

        // console.log("file ==> ",file)

        // 限制类型
        let imgTypes = ['png','jpg','jpeg','gif'];
        let type = file.file.type.split('/')[1];
        if(imgTypes.indexOf(type) === -1){
          this.$toast(`图片格式只支持${imgTypes.join(',')}`)
          return;
        }

        // 限制大小
        if(file.file.size > this.maxSize){
          this.$toast(`上传图片大小要小于${this.maxSize / 1024}kb`);
          return;
        }

        // // console.log("type ==> ",type)
        let base64 = file.content.replace(/data:image\/[A-Za-z]+;base64,/,"");
        
        this.$toast.loading({
          message: '加载中...',
          forbidClick: true,
          duration: 0,
          loadingType: 'spinner'
        })

        let data = utils.queryString({
          appkey: this.appkey,
          tokenString: tk,
          imgType: type,
          serverBase64Img: base64
        })

        this.axios({
          method: "POST",
          url: "/updateUserBg",
          data
        }).then( result => {
          // console.log("updateUserBg result ==> ",result)
          this.$toast.clear();
          this.$toast(result.data.msg);
          if(result.data.code == "I001"){
            this.userData.userBg = result.data.userBg;
            this.show = false;
          }
        }).catch( err => {
          this.$toast.clear();
          // console.log("updateUserBg err ==> ",err)
        })

      },
      // 退出登录
      logout(){
        let tk = localStorage.getItem('CSTK');
        // 不存在token
        if(!tk){
          return;
        }

        this.$toast.loading({
          message: '加载中...',
          forbidClick: true,
          duration: 0,
          loadingType: 'spinner'
        })

        let data = utils.queryString({
          appkey: this.appkey,
          tokenString: tk,
        })

        this.axios({
          method: "POST",
          url: "/logout",
          data
        }).then( result => {
          // console.log("logout result ==> ",result)
          this.$toast.clear();
          this.$toast(result.data.msg);
          if(result.data.code == "F001"){
            localStorage.removeItem('CSTK')
            this.userData = [];
            this.isLogin = false;
          }
        }).catch( err => {
          this.$toast.clear();
          // console.log("logout err ==> ",err)
        })
      },
      // 弹出确认退出登录按钮
      showDeletePopup(){
        this.showDelete = true;
        this.$dialog.confirm({
          message: '确认退出登录？',
        })
          .then(() => {
            // on confirm
            this.logout();
          })
          .catch(() => {
            // on cancel
          });        
      },
      // 跳转路由
      toline(name){
        if(this.isLogin){
          this.$router.push({name})
        }else{
          this.$router.push({name: "Login"})
        }
      }
      

      



    }

  }
</script>
